import styled from 'styled-components';
import Box from '@material-ui/core/Box';
export const StyledBox = styled(Box)`
  margin-left: 0px;
  margin-right: 0px;
  display: inline-flex;
  flex-direction: column;
  width: 100%;
  gap: 15px;
  a {
    text-decoration: none;
    color: inherit;
  }
`;
